import { useEffect, useRef } from 'react';
import './index.less';

export default function RevealText() {
  const text = useRef();

  useEffect(() => {
    const { current } = text;
    const letters = current.textContent.split('');
    current.textContent = '';
    const middle = letters.filter(c => c !== ' ').length / 2;
    letters.forEach((letter, i) => {
      const span = document.createElement('span');
      span.textContent = letter;
      span.style.animationDelay = `${0.3 + Math.abs(i-middle)*0.1}s`;
      current.appendChild(span);
    });
  }, []);

  return (
    <div className="wrapper reveal-text">
      <h4>Reveal Text</h4>
      <main>
        <p ref={text}>sword art online</p>
      </main>
    </div>
  )
}